.form-signin {
  width: 100%;
  min-width: 300px;
  max-width: 370px;
  padding: 15px;
  margin: 0 auto;

  .checkbox {
    font-weight: 400;
  }
  .form-control {
    position: relative;
    box-sizing: border-box;
    height: auto;
    padding: 10px;
    font-size: 16px;
  }
  .form-control:focus {
    z-index: 2;
  }
  input[type="email"] {
    margin-bottom: -1px;
    border-bottom-right-radius: 0;
    border-bottom-left-radius: 0;
  }
  input[type="password"] {
    margin-bottom: 10px;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
  }
}
.form-signup {
  width: 100%;
  max-width: 300px;
  padding: 15px;
  margin: 0 auto;

  .checkbox {
    font-weight: 400;
  }
  .form-control {
    position: relative;
    box-sizing: border-box;
    height: auto;
    padding: 10px;
    font-size: 16px;
  }
  .form-control:focus {
    z-index: 2;
  }
  input[name="name"] {
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
  }
  select#gender {
    border-top-left-radius: 0;
    border-top-right-radius: 0;
  }
}
